"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { Lightbulb } from "lucide-react";

export default function DiscoverMore() {
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    // Load discover content from API (database)
    const loadDiscoverContent = async () => {
      try {
        const response = await fetch("/api/settings/homepage");
        if (response.ok) {
          const data = await response.json();
          if (data.content && data.content.discover && Array.isArray(data.content.discover) && data.content.discover.length > 0) {
            setCategories(data.content.discover);
            return;
          }
        }
      } catch (error) {
        console.error("Error loading discover content from API:", error);
      }

      // Fallback to localStorage
      if (typeof window !== "undefined") {
        const savedContent = localStorage.getItem("homepageSectionContent");
        if (savedContent) {
          try {
            const parsed = JSON.parse(savedContent);
            if (parsed.discover && Array.isArray(parsed.discover) && parsed.discover.length > 0) {
              setCategories(parsed.discover);
              return;
            }
          } catch (error) {
            console.error("Error parsing discover content:", error);
          }
        }
        // Default categories if none exist
        setCategories([
          {
            id: "1",
            name: "Startup",
            links: [
              { id: "1", title: "How Speedata Is Powering Next Gen AI and Big Data With APU Chips", url: "/posts/startup-1" },
              { id: "2", title: "SGA marks 15 years of redefining communications consulting in India", url: "/posts/startup-2" },
              { id: "3", title: "From Hackathon to Lifesaving: How EmerReady Prepares Kids for...", url: "/posts/startup-3" },
              { id: "4", title: "Woven by tradition: How Taavi is bringing India's craft heritage into the...", url: "/posts/startup-4" },
            ],
          },
          {
            id: "2",
            name: "Technology",
            links: [
              { id: "1", title: "Outlook 2026: India's semiconductor push clears capacity hurdle", url: "/posts/tech-1" },
              { id: "2", title: "India launches DHRUV64, first indigenously designed 1.0 GHz 64-bit", url: "/posts/tech-2" },
              { id: "3", title: "Build an AI-powered business with 40% off on Salesforce's core platforms", url: "/posts/tech-3" },
              { id: "4", title: "Tata, Intel sign MoU to build India centred silicon and compute ecosystem", url: "/posts/tech-4" },
            ],
          },
          {
            id: "3",
            name: "Business Ideas",
            links: [
              { id: "1", title: "5 Profitable small business ideas for new founders", url: "/posts/business-1" },
              { id: "2", title: "This Indian Tree Can Make You Richer Than Sugarcane or Wheat", url: "/posts/business-2" },
              { id: "3", title: "The Day KFC Ran Out of Chicken - And Turned Crisis into a Comeback", url: "/posts/business-3" },
              { id: "4", title: "Meet the 10 entrepreneurs set to drive change in 2025", url: "/posts/business-4" },
            ],
          },
          {
            id: "4",
            name: "Resources",
            links: [
              { id: "1", title: "These businesses are turning traditional Indian sweets into health conscious", url: "/posts/resources-1" },
              { id: "2", title: "How brands are blending tech with tradition this Ganesh Chaturthi 2025", url: "/posts/resources-2" },
              { id: "3", title: "What to consider before taking a business loan?", url: "/posts/resources-3" },
              { id: "4", title: "Must read business newsletters: An entrepreneur's secret weapon", url: "/posts/resources-4" },
            ],
          },
        ]);
      }
    };

    loadDiscoverContent();
    // Refresh every 30 seconds to get updates
    const interval = setInterval(loadDiscoverContent, 30000);

    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <section className="bg-white dark:bg-slate-900 border-b border-gray-200 dark:border-slate-800 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="text-center mb-12">
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-bold text-black dark:text-white inline-flex items-center">
            Discover M
            <span className="relative inline-block">
              <span className="relative z-10">o</span>
              <Lightbulb className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-6 h-6 md:w-8 md:h-8 text-yellow-500 dark:text-yellow-400" fill="currentColor" />
            </span>
            re
          </h2>
        </div>

        {/* Categories Grid - 3 rows, 4 columns */}
        {categories.length > 0 ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {categories.map((category, index) => (
              <div key={category.id || index} className="space-y-4">
                {/* Category Header */}
                <div>
                  <h3 className="text-xl font-bold text-black dark:text-white mb-2">
                    {category.name}
                  </h3>
                  <div className="h-px bg-gray-300 dark:bg-slate-700"></div>
                </div>

                {/* Links List */}
                {category.links && category.links.length > 0 ? (
                  <ul className="space-y-3">
                    {category.links.map((link, linkIndex) => (
                      <li key={link.id || linkIndex}>
                        <Link
                          href={link.url || "#"}
                          className="block text-sm text-gray-700 dark:text-gray-300 hover:text-blue-600 dark:hover:text-blue-400 transition-colors leading-relaxed"
                        >
                          {link.title}
                        </Link>
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-xs text-gray-400 dark:text-gray-500 italic">No links added</p>
                )}
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-12">
            <p className="text-gray-500 dark:text-gray-400">No categories configured</p>
            <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">Add categories and links in the admin panel</p>
          </div>
        )}
      </div>
    </section>
  );
}
