"use client";

import { useEffect, useState } from "react";
import Navbar from "@/components/Navbar";
import HeroFeatured from "@/components/HeroFeatured";
import SpotLight from "@/components/SpotLight";
import Featured from "@/components/Featured";
import Explore from "@/components/Explore";
import TrendingNow from "@/components/TrendingNow";
import DiscoverMore from "@/components/DiscoverMore";
import Recent from "@/components/Recent";
import Footer from "@/components/Footer";

// Component mapping
const sectionComponents = {
  HeroFeatured: HeroFeatured,
  SpotLight: SpotLight,
  Featured: Featured,
  Explore: Explore,
  TrendingNow: TrendingNow,
  Recent: Recent,
  DiscoverMore: DiscoverMore,
};

// Default order (Recent is not included as it's always shown and not customizable)
const defaultOrder = [
  { id: "hero", name: "Hero", component: "HeroFeatured", enabled: true },
  { id: "spotlight", name: "Spotlight", component: "SpotLight", enabled: true },
  { id: "featured", name: "Featured", component: "Featured", enabled: true },
  { id: "explore", name: "Explore", component: "Explore", enabled: true },
  { id: "trending", name: "Trending Now", component: "TrendingNow", enabled: true },
  { id: "discover", name: "Discover More", component: "DiscoverMore", enabled: true },
  ];

export default function Home() {
  const [sectionOrder, setSectionOrder] = useState(defaultOrder);

  useEffect(() => {
    // Load section order from API (database)
    const loadSectionOrder = async () => {
      try {
        const response = await fetch("/api/settings/homepage");
        if (response.ok) {
          const data = await response.json();
          if (data.order && Array.isArray(data.order)) {
            // Filter out header, footer, and recent (they are always fixed)
            // Recent is always shown and not customizable
            const filtered = data.order.filter((s) => 
              s.id !== "header" && 
              s.id !== "footer" && 
              s.id !== "recent"
            );
            setSectionOrder(filtered);
            return;
          }
        }
      } catch (error) {
        console.error("Error loading section order from API:", error);
      }

      // Fallback to localStorage
      if (typeof window !== "undefined") {
        const savedOrder = localStorage.getItem("homepageSectionOrder");
        if (savedOrder) {
          try {
            const parsed = JSON.parse(savedOrder);
            // Filter out header, footer, and recent (they are always fixed)
            // Recent is always shown and not customizable
            const filtered = parsed.filter((s) => 
              s.id !== "header" && 
              s.id !== "footer" && 
              s.id !== "recent"
            );
            setSectionOrder(filtered);
          } catch (error) {
            console.error("Error parsing section order:", error);
          }
        }
      }
    };

    loadSectionOrder();
    // Refresh every 30 seconds to get updates
    const interval = setInterval(loadSectionOrder, 30000);

    return () => clearInterval(interval);
  }, []);

  // Render sections based on order
  const renderSections = () => {
    const sections = sectionOrder
      .filter((section) => section.enabled)
      .map((section) => {
        const Component = sectionComponents[section.component];
        if (!Component) {
          console.warn(`Component ${section.component} not found`);
          return null;
        }
        return <Component key={section.id} />;
      });
    
    // Always add Recent section at the end (not customizable)
    return [...sections, <Recent key="recent" />];
  };

  return (
    <>
      <Navbar />
      {renderSections()}
      <Footer />
    </>
  );
}
